<template>
    <div class="code">
        <headerBar :title="headerNavData"></headerBar>
        <div class="code_top">
          <!-- <img :src="src"> -->
          <canvas id="drawing" :style="{width: '1rem', height: '1rem'}" style="border-radius: 50%;margin-bottom: .15rem"></canvas>
          <p>{{message}}</p>
        </div>
        <div class="code_center">
          <!-- <img src="../assets/images/code.png"> -->
          <div id="qrcode"></div>
          <button type="button" @click="vueCopy()" class="copy_btn">{{$t('code.codeaddress')}}</button>
            <!-- <button type="button"
            v-clipboard:copy="message"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError">{{$t('code.codeaddress')}}</button> -->
        </div>
    </div>
  
</template>

<script>
  import QRCode from 'qrcodejs2'
  import {getCookie} from '../assets/js/cookie.js' 
  import {AndroidCopytent} from '@/api/android'
  import headerBar from './header.vue'
  export default {
    components:{
      "headerBar":headerBar
    },
    created(){
      this.message = getCookie('publickey')
    },
    data () {
      return {
        message:'',
        src:'',
        headerNavData:{
          left:true,
          title:this.$t('code.mcode'),
          right:false,
          bj:'rgba(255,255,255,0)',
          search:false,
          share:false
        }
      }
    },
    methods:{
      //生成二维码
      qrcode () {  
        var that = this
          let qrcode = new QRCode('qrcode', {  
              width: 200,  
              height: 200, 
              text: that.message 
          }) 
      }, 
      vueCopy(){
        var that = this
        AndroidCopytent(this.message,function(isbuloon){
          if(isbuloon){
            that.$dialog.toast({
                mes: that.$t('data.copycontent'),
                timeout: 2000
            });
          }else{
            that.$dialog.toast({
                mes: that.$t('data.copycontentfail'),
                timeout: 2000
            });
          }
        })
      },
      onCopy: function (e) {
        this.$dialog.toast({
            mes: that.$t('data.copycontent'),
            timeout: 2000
        });
      },
      onError: function (e) {
        this.$dialog.toast({
            mes: that.$t('data.copycontentfail'),
            timeout: 2000
        });
      }
    },
    mounted () {  
      this.qrcode()
      var codelist = getCookie("codelist")
      if (codelist&&codelist!=""&&codelist!="null"&&codelist!="undefined"){
          this.DrawRandomPic(getCookie("publickey").substring(2,42),"drawing")
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.code{
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.code_top{
  padding: 2.1rem 0 .35rem;
  background: url('../assets/images/codebj.png') no-repeat top left;
  background-size: 100%;
  text-align: center;
}
.code_top img{
  width: .85rem;
  height: .85rem;
  border-radius: 50%;
  margin-bottom: .35rem
}
#qrcode{
  width: 200px;
  height: 200px;
  margin: 1.5rem auto .35rem
}
.code_top p{
  width: 80%;
  margin: 0 auto;
  white-space: wrap;
  font-size: .26rem;
  color: #fff;
  /*letter-spacing: 1px*/
}
.code_center{
  text-align: center;
}
.code_center img{
  width: 4rem;
  height: 4rem;
  margin: 1.5rem auto .35rem;
}
.code_center button{
  width: 4rem;
  color: #fff;
  font-size: .28rem;
  padding: .15rem 0;
  outline: none;
  border:none;
  background-color: #278eed;
  border-radius: 5px;
}
.code_center button:active{
  background-color: #337cc0;
}
</style>



